@import url(https://fonts.googleapis.com/css?family=Courgette|Lato);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer {
    /* height: 20px; */
    padding: 10px;
    background: #fff;
    margin-top: 15px;
}

.copyright {
    margin: 0;
    margin-left: 20px;
    font-style: italic;
    font-size: 14px;
}

.main {
    text-align: center;
}

.logo.blank-page {
    margin: 0 auto;
    height: 50vmin;
    padding: 20vh 0;
}

.meal-plan {
    width: 85%;
    max-width: 750px;
    margin: 0 auto;
    padding: 20px; 
}

.meal-plan img {
    height: 100px;
}

.title {
    margin: 5px auto;
}

.loaded {
    margin: 0;
}

.welcome {
    color: #aaa; 
    font: 400 22px 'Courgette', cursive;
}

.display {
    padding: 0;
}

.display li {
    font-size: 18px;
    font-weight: 700;
    margin: 20px auto; 
    padding: 15px 30px;
    list-style-type: none;
    text-align: left; 
    background: #fff;
    box-shadow: 0 0 10px #666;
    -webkit-transition: all 1s;
    transition: all 1s;
    cursor: pointer;
}

.display li span {
    width: 80%;
    display: inline-block;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
    /* z-index: -1; */
}

.display li:after {
    font: 700 18px 'Lato'; 
    color: #bbb; 
    float: right;
}
.display li[data-day="0"]:after { content: "Sun"; }
.display li[data-day="1"]:after { content: "Mon"; }
.display li[data-day="2"]:after { content: "Tues"; }
.display li[data-day="3"]:after { content: "Wed"; }
.display li[data-day="4"]:after { content: "Thur"; }
.display li[data-day="5"]:after { content: "Fri"; }
.display li[data-day="6"]:after { content: "Sat"; }

.display li:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    /* box-shadow: 0 0 30px #a9da84; */
}

.display li.editing {
    /* background: #ebffda; */
    box-shadow: 0 0 20px #79a05c;
}

.display li.editing.selected {
    background: #a9da84;
    color: #fff;
    text-shadow: 0 1px 1px #333;
}

.display li.editing.selected:after {
    color: #fff;
    text-shadow: 0 1px 1px #333;
}

.actions button {
    display: inline-block; 
    margin: 10px;
    min-width: 120px;
    font-size: 18px;
    font-weight: 700;
    border-radius: 8px;
    padding: 10px;
    color: #fff; 
    cursor: pointer;
    width: 90%;
}

.actions button:focus {
    outline: none;
}


.swap {
    background: #76B141; 
    border: 2px solid #76B141; 
}

.swap:hover {
    background: #fff; 
    color: #76B141;
}

.createNew {
    background: #0B8343; 
    border: 2px solid #0B8343; 
    display: inline-block; 
    margin: 10px;
    min-width: 120px;
    font-size: 18px;
    font-weight: 700;
    border-radius: 8px;
    padding: 10px;
    color: #fff; 
    cursor: pointer;
}

.createNew:hover, .createNew:hover a {
    background: #fff; 
    color: #0B8343;
}

.createNew a {
    text-decoration: none; 
    color: #fff;
}

.shuffle {
    background: #5ba066; 
    border: 2px solid #5ba066; 
}

.shuffle:hover {
    background: #fff; 
    color: #5ba066;
}

.special-actions button {
    width: 90%;
    height: 65px;
    max-width: 400px;
    background: #758b7f;
    border: 2px solid #758b7f;
}

.special-actions button:hover  {
    color: #758b7f;
    background: #fff;
}


#list-modal.active {
    position: fixed; 
    top: 0; 
    bottom: 0; 
    left: 0; 
    right: 0; 
    background: #eee;
    /* background-image: url('../../logo.png');  */
    /* background-repeat: no-repeat; 
    background-position: center;
    background-size: 85% 90%;  */
    padding: 60px 40px 40px;
    text-align: left;
    overflow-y: scroll;     
}

#list-modal.active:after {
    content: ''; 
    background-image: url(/static/media/logo.9cadaf43.png); 
    background-repeat: no-repeat; 
    background-position: center;
    background-size: 90% 90%; 
    position: absolute; 
    top: 0; 
    bottom: 0; 
    right: 0; 
    left: 0; 
    z-index: -1;
    opacity: .1;


}

#close-modal {
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
    position: fixed; 
    top: 20px; 
    right: 35px;
    height: 60px; 
    cursor: pointer;
}

#list-modal {
}

#list-modal .meal ul {
    text-decoration: underline;
    font: 700 24px 'Lato', sans-serif;  
    padding: 0;
    margin: 10px;
    /* z-index: 2; */
    /* display: block; */

}

#list-modal .meal li {
    font: 400 20px 'Lato', sans-serif; 
    padding: 5px 0 0 10px; 
}

@media (min-width: 500px) {
    .actions button {
        width: 28%;
    }

    .special-actions button {
        width: 90%;
    }
}
.sign-in {
    position: fixed; 
    top: 10%; 
    left: 0; 
    right: 0; 
    min-height: 400px;
    height: auto;
    width: 360px;
    text-align: center; 
    margin: 0 auto;
    padding: 30px 20px;
    box-sizing: border-box;
    background: #fff;   
    box-shadow: 0 0 5px #ccc;
    z-index: 2;
}

.sign-in .logo {
    height: 200px;
}

.heading {
    font: 700 18px 'Lato'; 
}

.overlay {
    z-index: 1; 
    position: fixed; 
    top: 0; 
    bottom: 0; 
    height: 100%;
    width: 100%;
    background: #333;
}

input:-webkit-autofill-selected {
    background: #d9ffbc !important;
}

#user-invalid {
    width: 80%;
    margin: 0 auto 5px;
    color: rgb(168, 21, 21);
    font-style: italic;
    font-size: 14px;
}

form input {
    display: block; 
    margin: 10px auto; 
    border-radius: 12px; 
    height: 35px;
    width: 200px;
    padding: 5px;
    box-sizing: border-box;
}

form input:focus {
    outline: none; 
    /* background: #d9ffbc; */
    /* border: 2px solid #6e8f56 */
}

form button {
    width: 200px;
    height: 40px;
    padding: 5px;
    border-radius: 12px;
    background: #0B8343; 
    color: #fff;
    font-weight: 700; 
    text-transform: uppercase;
    cursor: pointer;
}

form button:focus {
    outline: none; 
}

.swap-sign-in {
    margin: 20px auto 0;
    color: rgb(28, 158, 209); 
    text-decoration: underline;
    cursor: pointer;
    font-size: 14px;
}

.signout-btn {
    width: 200px;
    height: 40px;
    padding: 5px;
    border-radius: 8px;
    background: #83810b; 
    border: 1px solid #83810b; 
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
    margin-top: 30px;
}

.signout-btn:hover  {
    color: #83810b; 
    background: #fff;
}

.signout-btn:focus {
    outline: none; 
}
.meal p {
    margin: 0;
    display: inline-block; 
    /* width: 50%; */
}

.meal {
    padding: 5px; 
    cursor: pointer;
    margin: 5px auto;
}

.meal img {
    height: 15px;
    width: 15px; 
    display: inline-block; 
    margin-left: 10px;
}

.add-title {
    font: 700 26px 'Lato', sans-serif; 
    margin-bottom: 40px;
}

.add-meal input {
    width: 100%;
    height: 45px;
    padding-left: 20px;
    font-size: 16px;
}

.add-meal label {
    display: block;
}

.add-meal .add-left {
    width: 55%;
    display: inline-block; 
}

.add-meal .add-right {
    margin-left: 5%;
    width: 30%;
    display: inline-block; 
}

.add-ingred {
    margin: 10px auto;
    width: 90%;
    counter-reset: count; 
}

.add-ingred input {
    width: 100%;
    padding-left: 30px;
    font-size: 16px;
}

.add-ingred label {
    position: relative; 
}

.add-ingred label::before {
    counter-increment: count;
    content: counter(count) '';
    color: #aaa;
    font-weight: 700;
    display: block; 
    position: absolute;
    left: 10px; 
    bottom: 13px; 
}

#invalid-time {
    margin: 20px auto; 
    color: rgb(168, 21, 21);
    font-style: italic;
    font-size: 16px;    
}

#success {
    margin: 20px auto; 
    color: #0B8343;
    font-size: 18px;    
}

#fail {
    margin: 20px auto; 
    color: rgb(199, 37, 37);
    font-size: 18px;    
}

.actions .add {
    background: #0B8343; 
    border:2px solid #0B8343; 
}

.actions .add:hover  {
    color: #0B8343;
    background: #fff;
}

.actions .cancel {
    background: #DDDDDD; 
    border: 2px solid #bbb; 
}

.actions .cancel:hover, .actions .cancel:hover a {
    background: #551A8B; 
    color: #DDDDDD;
}

.actions .cancel a {
    text-decoration: none; 
    color: #551A8B; 
}

.actions .update {
    background: #199797; 
    border:2px solid #199797; 
}

.actions .update:hover  {
    color: #199797;
    background: #fff;
}

.actions .delete {
    background: #971919; 
    border: 2px solid #971919; 
}

.actions .delete:hover  {
    color: #971919;
    background: #fff;
}

.user-meals {
    -webkit-columns: 2;
            columns: 2; 
}
